export default {
	data() {
		return {
			selectArr: [],
			result: {},
			menuIndex: 0,
			selectDetailList: []
		};
	},
	props: {
		color: {
			type: String,
			default () {
				return '#666666'
			}
		},
		themeColor: {
			type: String,
			default () {
				return '#D1372C'
			}
		},
		menuList: {
			type: Array,
			default () {
				return []
			}
		}
	},
	computed:{
		selectedObj() {
			let obj = {}
			for (let i = 0;i < this.menuList.length;i++) {
				let item = this.menuList[i];
				if (item.isMutiple) {
					obj[item.key] = [];
				} else{
					obj[item.key] = '';
				}
			}
			return obj;
		}
	},
	methods: {
		menuTabClick(index) {
			this.menuIndex = index;
			this.selectDetailList = this.menuList[index].detailList
		},
		itemTap(index, list, isMutiple, key) {
			if (isMutiple == true) {
				list[index].isSelected = !list[index].isSelected;
				if (index == 0) {
					this.resetSelected(list, key)
				} else {
					list[0].isSelected = false
					if (list[index].isSelected) {
						this.selectedObj[key].push(list[index].value);
					} else {
						list[index].isSelected = false;
						var idx = this.selectedObj[key].indexOf(list[index].value);
						this.selectedObj[key].splice(idx, 1);
					}
					this.result = this.selectedObj;
				}
			} else {
				if (index == 0) {
					this.resetSelected(list, key)
				} else {
					list[0].isSelected = false
					this.selectedObj[key] = list[index].value;
					this.result = this.selectedObj;
					for (let i = 0; i < list.length; i++) {
						if (index == i) {
							list[i].isSelected = true
						} else {
							list[i].isSelected = false
						}
					}
				}
			}
		},
		resetSelected(list, key) {
			if (typeof this.result[key] == 'object') {
				this.result[key] = [];
			} else{
				this.result[key] = '';
			}
			for (let i = 0; i < list.length; i++) {
				if (i == 0) {
					list[i].isSelected = true;
				} else {
					list[i].isSelected = false;
				}
			}
		},
		sortTap(index, list, key) {
			this.result[key] = list[index].value;
			for (let i = 0; i < list.length; i++) {
				if (index == i) {
					list[i].isSelected = true;
				} else {
					list[i].isSelected = false;
				}
			}
			this.$emit("confirm", this.result);
		},
		sureClick() {
			this.$emit("confirm", this.result);
		},
		resetClick(list,key) {
			this.resetSelected(list,key)
		}
	}
}
